import{Observable}from"../Observable";import{Subscription}from"../Subscription";export function schedulePromise(input,scheduler){return new Observable((function(subscriber){var sub=new Subscription;return sub.add(scheduler.schedule((function(){return input.then((function(value){sub.add(scheduler.schedule((function(){subscriber.next(value),sub.add(scheduler.schedule((function(){return subscriber.complete()})))})))}),(function(err){sub.add(scheduler.schedule((function(){return subscriber.error(err)})))}))}))),sub}))}