import{scan}from"./scan";import{takeLast}from"./takeLast";import{defaultIfEmpty}from"./defaultIfEmpty";import{pipe}from"../util/pipe";export function reduce(accumulator,seed){return arguments.length>=2?function(source){return pipe(scan(accumulator,seed),takeLast(1),defaultIfEmpty(seed))(source)}:function(source){return pipe(scan((function(acc,value,index){return accumulator(acc,value,index+1)})),takeLast(1))(source)}}